.page {
  height: 100%;
  display: flex;
  flex-direction: column;
  margin: 0;
  background-color: @page;
  background-image: url("../img/white-grad-left.png"), url("../img/white-grad-right.png");
  background-position: left -350px top, right top;
  background-repeat: no-repeat, no-repeat;
}

.page__wrapper {
  margin: 0 auto;
  width: 100%;
}

.page__upper {
  .center-desktop();
}

@media (max-width: @mobile-width) {
  .page__upper {
    .center-mobile();
  }

  .page {
    background-image: none;
  }
}